<template>
  <div>
    <h2> My Blog </h2>
    <div v-for="(item, index) in blogs" :key="index">
      <article-view :item="item" :url="'#/blog/' + index"/>
    </div>
  </div>
</template>

<script>
import { getBlog, blogNum } from '@/data/load';
import articleView from '@/components/ArticlePreview';

export default {
  components: {
    articleView,
  },
  data() {
    return {
      blogs: [],
    };
  },
  methods: {
    getBlog,
  },
  mounted() {
    for (let i = 0; i < blogNum; i += 1) {
      this.getBlog(i).then((x) => { this.blogs.push(x); });
    }
  },
};
</script>



