<template>
  <div v-if="item !== null">
    <h3> {{ item.title }} </h3>
    <div v-html="item.content.substring(0, 100) + (item.content.length >= 100 ? '...':'')"/>
    <a :href="url">Read More</a>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      default: null,
      type: Object,
    },
    url: {
      default: '',
      type: String,
    },
  },
};
</script>



